import { useMutation } from "@tanstack/react-query"
import { createParking } from "../services"
import { getCreateParkingRetryDelay } from "../utils/getCreateParkingRetryDelay"

const useCreateParking = () => {
  return useMutation({
    mutationFn: createParking,
    retry: 1,
    retryDelay: getCreateParkingRetryDelay,
  })
}
export { useCreateParking }
